.icon {
  display: block;

  width: 24px;
  height: 24px;

  background-repeat: no-repeat;
  background-size: cover;

  transition: 0.3s;

  &:hover {
    transform: scale($hover-size);
  }
}

.icon--menu {
  background-image: url(/src/images/menu.png);

  @include on-small-desktop {
    display: none;
  }
}

.icon--close {
  background-image: url(/src/images/icon-close.png);
}
